<template>
<div>
  <div class="ui two column stackable grid">
    <div class="column">
      <add-question :space="space" button-class="primary"></add-question>
    </div>
    <div class="column">
      <filter-bar></filter-bar>
    </div>
  </div>
  <vuetable ref="vuetable" v-bind:api-url="apiUrl" :fields="fields" pagination-path="" @vuetable:pagination-data="onPaginationData" detail-row-component="my-detail-row" @vuetable:cell-clicked="onCellClicked" track-by="_id" :append-params="moreParams" :http-options="httpOptions">
  </vuetable>
  <vuetable-pagination ref="pagination" @vuetable-pagination:change-page="onChangePage"></vuetable-pagination>
</div>
</template>

<script>
import Vue from 'vue'
import Vuetable from 'vuetable-2/src/components/Vuetable.vue'
import moment from 'moment'
import VuetablePagination from 'vuetable-2/src/components/VuetablePagination.vue'
import DetailRow from './DetailRow.vue'
import FilterBar from './FilterBar.vue'
import addQuestion from './addQuestion.vue'
import axios from 'axios'
import mapState from 'vuex'

Vue.component('filter-bar', FilterBar)
Vue.component('my-detail-row', DetailRow)
Vue.component('Vuetable', Vuetable)
Vue.component('addQuestion', addQuestion)
Vue.component('VuetablePagination', VuetablePagination)

export default {
  name: 'spacetable',
  props: ['space'],
  components: {
    Vuetable,
    VuetablePagination,
    DetailRow,
    FilterBar,
    addQuestion
  },
  data() {
    return {
      fields: [{
        name: 'sequence',
        title: '#',
        sortField: 'sequence',
        titleClass: 'center aligned one wide',
        dataClass: 'center aligned'
      }, {
        name: 'text',
        title: 'Question',
        titleClass: 'center aligned'
      }, {
        name: 'displayName',
        title: 'Questioner',
        sortField: 'displayName',
        titleClass: 'center aligned three wide'
      }, {
        name: 'createdOn',
        title: 'Date',
        callback: 'formatDate|MM-DD-YYYY',
        sortField: 'createdOn',
        titleClass: 'center aligned two wide'
      }, {
        name: 'answers.length',
        title: 'Answers',
        titleClass: 'center aligned one wide',
        dataClass: 'center aligned'
      }],
      moreParams: {},
      httpOptions: {
        withCredentials: true
      }
    }
  },
  computed: {
    apiUrl() {
      let getUrl = this.$store.state.apiUrl + this.space;
      return getUrl;
    },
    deleteUrl() {
      return this.$store.state.baseUrl + '/api/questions/';
    }
  },
  mounted() {
    this.$nextTick(function() {
      this.$events.$on('filter-set', eventData => this.onFilterSet(eventData))
      this.$events.$on('filter-reset', e => this.onFilterReset())
    })
  },
  methods: {
    formatDate(value, fmt = 'D MMM YYYY') {
      return (value == null) ? '' : moment(value, 'YYYY-MM-DD').format(fmt)
    },
    onPaginationData(paginationData) {
      this.$refs.pagination.setPaginationData(paginationData)
    },
    onChangePage(page) {
      this.$refs.vuetable.changePage(page)
    },
    onCellClicked(data, field, event) {
      this.$refs.vuetable.toggleDetailRow(data._id)
    },
    onFilterSet(filterText) {
      this.moreParams.filter = filterText
      this.$nextTick(() => this.$refs.vuetable.refresh())
    },
    onFilterReset() {
      delete this.moreParams.filter
      this.$nextTick(() => this.$refs.vuetable.refresh())
    },
    onAction(action, data, index) {
      if (action == 'delete-item') {
        axios.delete(this.deleteUrl + data._id, {
            withCredentials: true
          })
          .then(response => {
            Vue.toast('Question removal successful.', {
              id: 'delete-question',
              className: 'et-info',
              horizontalPosition: 'center',
              verticalPosition: 'bottom',
              duration: 5000,
              mode: 'queue',
              transition: 'slide-up'
            })
            this.$nextTick(() => this.$refs.vuetable.refresh())
          })
          .catch(err => {
            Vue.toast('Error removing question', {
              id: 'delete-question',
              className: 'et-warn',
              horizontalPosition: 'center',
              verticalPosition: 'bottom',
              duration: 5000,
              mode: 'queue',
              transition: 'slide-up'
            })
          })
      }
    }
  }
}
</script>



