<template>
<div class="ui container bs-docs-header">
  <h1>Spark Space:
        <span>{{msg}}</span>
      </h1>
  <p>Automatically Generated FAQ Page</p>
</div>
</template>

<script>
import Vue from 'vue';
import axios from 'axios';

let msg = '';

export default {
  name: 'banner',
  props: ['space'],
  computed: {
    apiUrl() {
      let path = 'detail/';
      let roomApi = this.$store.state.apiUrl + path + this.space;
      return roomApi;
    }
  },
  mounted: function() {
    this.setupCharts()
  },
  methods: {
    setupCharts() {
      axios.get(this.apiUrl, {
        withCredentials: true
      }).then(response => {
        this.msg = response.data.displayName
      });
    }
  },
  data() {
    return {
      msg: msg
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bs-docs-header {
  margin-bottom: 40px;
  font-size: 20px;
}

.bs-docs-header {
  position: relative;
  padding: 30px 0;
  color: #f4f9ff;
  text-align: center;
  text-shadow: 0 1px 0 rgba(0, 0, 0, .1);
  background-color: ##367bc9;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3d597c), to(#367bc9));
  background-image: -webkit-linear-gradient(top, #3d597c 0, #367bc9 100%);
  background-image: -o-linear-gradient(top, #3d597c 0, #367bc9 100%);
  background-image: linear-gradient(to bottom, #3d597c 0, #367bc9 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#3d597c', endColorstr='#367bc9', GradientType=0);
  background-repeat: repeat-x;
}
</style>



