import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import config from './config'
Vue.use( Vuex )

const store = new Vuex.Store( {
  state: {
    isAuthenticated: false,
    baseUrl: config.base,
    currentUser: {},
    userSpaces: [],
    userSpacesLoaded: false,
    apiUrl: config.base + '/api/spaces/'
  },
  actions: {
    loadUser: function ( context ) {
      axios.get( context.getters.userUrl, {
        withCredentials: true
      } ).then( ( response ) => {
        context.commit( 'setUser', response.data )
      }, ( err ) => {
        console.error( err )
      } )
    },
    loadAuthentication: function ( context ) {
      axios.get( context.getters.authUrl, {
        withCredentials: true
      } ).then( ( response ) => {
        context.commit( 'setAuthentication', response.data.isAuthenticated )
      }, ( err ) => {
        console.error( err )
      } )
    },
    loadSpaces: function ( context ) {
      axios.get( context.getters.spacesUrl, {
        withCredentials: true
      } ).then( ( response ) => {
        context.commit( 'setSpaces', response.data )
      }, ( err ) => {
        console.error( err )
      } )
    },
    logOut: function ( context ) {
      axios.get( context.getters.logoutUrl, {
        withCredentials: true
      } ).then( ( response ) => {
        context.commit( 'setLogout', response.data.loggedOut )
      }, ( err ) => {
        console.error( err )
      } )
    },
    setSpacesLoading: function ( context ) {
      context.commit( 'setSpacesLoading', true )
    }
  },
  mutations: {
    setUser: ( state, user ) => {
      state.currentUser = user
    },
    setAuthentication: ( state, isAuthenticated ) => {
      state.isAuthenticated = isAuthenticated
    },
    setSpaces: ( state, response ) => {
      state.userSpaces = response
      state.userSpacesLoaded = true
    },
    setSpacesLoading: ( state, value ) => {
      state.userSpacesLoaded = false
    },
    setLogout: ( state, loggedOut ) => {
      state.isAuthenticated = !loggedOut
    }
  },
  getters: {
    authUrl: state => {
      return state.baseUrl + '/auth/isAuthenticated'
    },
    loginUrl: state => {
      return state.baseUrl + '/auth/login'
    },
    logoutUrl: state => {
      return state.baseUrl + '/auth/logout'
    },
    userUrl: state => {
      return state.baseUrl + '/auth/getUser'
    },
    spaceUrl: state => {
      return state.apiUrl + 'listSpaces'
    },
    spacesUrl: state => {
      return state.baseUrl + '/api/listSpaces'
    }
  },
  modules: {

  }
} )


export default store



