import Vue from 'vue'
import VueRouter from 'vue-router'
import about from './components/about.vue'
import logout from './components/logout.vue'
import spaces from './components/spaces.vue'
import space from './components/space.vue'
import login from './components/login.vue'
import error from './components/error.vue'
import help from './components/help.vue'
import axios from 'axios'
import config from './config'

let authUrl = config.base + '/auth/isAuthenticated'

var checkAuth = ( to, from, next ) => {
  axios.get( authUrl, {
    withCredentials: true
  } ).then( ( response ) => {
    if ( response.data.isAuthenticated )
      next();
    else {
      window.sessionStorage.setItem( 'redirect', true )
      window.sessionStorage.setItem( 'path', to.path )
      next( {
        path: '/login'
      } );
    }
  }, ( err ) => {
    console.error( err )
  } )
}

const routes = [
  {
    path: '/',
    component: about
    },
  {
    path: '/about',
    component: about
    },
  {
    path: '/space/:space',
    component: space,
    props: true,
    beforeEnter: checkAuth
    },
  {
    path: '/spaces',
    component: spaces,
    beforeEnter: checkAuth
    },
  {
    path: '/logout',
    component: logout
    },
  {
    path: '/login',
    component: login
    },
  {
    path: '/error',
    component: error
    },
  {
    path: '/help',
    component: help
    }
]

const linkActiveClass = 'active';

const router = new VueRouter( {
  routes,
  linkActiveClass
} )

export default router



