<style>



</style>

<template>
<div class="ui text container">
  <div class="ui warning message">
    <div class="ui icon header centered">
      <i class="lock icon"></i>
      <div class="content">
        You must authenticate to continue.
      </div>
      <p></p>
      <div class="ui sub header">Inquire requires that users have rights to access the FAQ content. </div>
    </div>
    <div>
      <p></p>
      <p></p>
      <p></p>
      <div class="ui sub header center aligned">Inquire will be requesting minimal permissions.
        <div class="ui sub header">Note: You will be leaving this site to log in. </div>
      </div>
      <br>
      <p></p>
      <div class="ui container center aligned">
        <button @click="redirect" class="ui primary button massive">Login</button>
      </div>
    </div>
  </div>
</div>
</div>
</template>

<script>
export default {
  data() {
    return {}
  },
  computed: {

  },
  methods: {
    redirect: function() {
      window.location.href = this.$store.getters.loginUrl
    }
  }
}
</script>



