<template>
<div class="ui form">
  <div class="field">
    <div class="ui action left icon input">
      <input type="text" v-model="filterText" @keyup.enter="doFilter" placeholder="Search questions">
      <i class="remove icon link" @click="resetFilter"></i>
      <button class="ui primary button" @click="doFilter">Search</button>
    </div>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      filterText: ''
    }
  },
  methods: {
    doFilter() {
      this.$events.fire('filter-set', this.filterText)
    },
    resetFilter() {
      this.filterText = '' // clear the text in text input
      this.$events.fire('filter-reset')
    }
  }
}
</script>



