<template>
<div :class="{loading: loading}" class="ui right floated primary icon button" @click="exportData">
  Export
  <i v-if="!successActive&&!errorActive" class="right chevron icon"></i>
  <i v-if="successActive" class="checkmark icon"></i>
  <i v-if="errorActive" class="remove icon"></i>
</div>
</template>


<script>
import json2csv from 'json2csv'
import axios from 'axios'

export default {
  data() {
    return {
      loading: false,
      successActive: false,
      errorActive: false
    }
  },
  props: {
    space: {
      type: String,
      required: true
    }
  },
  methods: {
    exportData() {
      this.loading = true
      var fields = [{
          label: 'Index',
          value: 'sequence',
          default: 'NULL'
        },
        {
          label: 'Question',
          value: 'text',
          default: 'NULL'
        },
        {
          label: 'Questioner',
          value: 'displayName',
          default: 'NULL'
        },
        {
          label: 'Date',
          value: 'createdOn',
          default: 'NULL'
        },
        {
          label: 'Answer',
          value: function(row, field, data) {
            if (row.answers[0]) {
              return row.answers[0].text;
            } else {
              return 'None'
            }
          },
          default: 'None' // default if value is not found (optional, overrides `defaultValue` for column)
        },
        {
          label: 'Answerer', // (optional, column will be labeled 'path.to.something' if not defined)
          value: function(row, field, data) {
            if (row.answers[0]) {
              return row.answers[0].displayName;
            } else {
              return 'N/A'
            }
          },
          default: 'N/A' // default if value is not found (optional, overrides `defaultValue` for column)
        },
        {
          label: 'Date', // (optional, column will be labeled 'path.to.something' if not defined)
          value: function(row, field, data) {
            if (row.answers[0]) {
              return row.answers[0].createdOn;
            } else {
              return 'N/A'
            }
          },
          default: 'N/A' // default if value is not found (optional, overrides `defaultValue` for column)
        }
      ]
      var data;
      axios.get(this.$store.state.apiUrl + '/' + this.space + '?per_page=500', {
        withCredentials: true
      }).then(response => {
        let options = {
          data: data,
          fields: fields
        }
        data = response.data.data
        try {
          var result = json2csv({
            data: data,
            fields: fields
          });
          var blob = new Blob([result], {
            type: 'text/csv'
          });
          var url = window.URL.createObjectURL(blob);
          window.location.href = url
          this.loading = false
          this.successActive = true
        } catch (err) {
          this.loading = false
          this.errorActive = true
          console.error(err);
        }
      }).catch(err => {
        this.loading = false
        this.errorActive = true
        console.log('Erorr getting API Data');
        console.error(err);
      })
    }
  }
}
</script>

<style>

</style>



